import './button.css';

interface ButtonProps {
    onClick: () => void;
    disabled?: boolean;
    className?: string;
    children: React.ReactNode;
    primary?: boolean;
    label?: string;
    size: 'small' | 'medium' | 'large';
    backgroundColor?: string;
    name: string;
    mode:any;
    style?: {};
}

import React from 'react';

const handleClick = () => {
    alert('Button clicked!');
};




const Button: React.FC<ButtonProps> = ({ onClick, disabled, className, size,mode,style, children }) => {
    return (
        <button
            style={{
                color: 'white',
                backgroundColor: 'skyblue',
                height: 30,
                width: 70,
                borderRadius: 10
            }}
            type="button"
            name="ButtonName"
            onClick={handleClick}
            disabled={disabled}
            className={['storybook-button', `storybook-button--${size}`, mode].join(' ')}
        >
            {children}
            Click Me
        </button>
    );
};

export default Button;
